import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DirectorySetupComponent } from './components/directory-setup/directory-setup.component';
import { MaintainDirectoryComponent } from './components/maintain-directory/maintain-directory.component';
import { AuthGuard } from '../../auth.guard';
import { ExternalDirectoryComponent } from './components/maintain-directory/external-directory/external-directory.component';
import { InternalDirectoryComponent } from './components/maintain-directory/internal-directory/internal-directory.component';
import { DirectoryAssetCategoryComponent } from './components/maintain-directory/directory-asset-category/directory-asset-category.component';


const routes: Routes = [
  { path: '', redirectTo: 'setup', pathMatch: 'full'},
  { path: 'setup', component: DirectorySetupComponent},
  { path: 'maintain', component: MaintainDirectoryComponent, canActivate: [AuthGuard],
  children: [
			{ path: '', redirectTo: 'external', pathMatch: 'full'},
			{ path: 'external', component: ExternalDirectoryComponent },
			{ path: 'internal', component: InternalDirectoryComponent },
			{ path: 'asset/:id', component: DirectoryAssetCategoryComponent },
			{ path: '**', redirectTo: 'external', pathMatch: 'full'}
]}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DirectoryRoutingModule { }
