import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

// import { AssetService } from '../../../../../api/services/asset.service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';


@Component({
  selector: 'app-directory-asset-category',
  templateUrl: './directory-asset-category.component.html',
  styleUrls: ['./directory-asset-category.component.scss']
})
export class DirectoryAssetCategoryComponent implements OnInit, AfterViewInit {

  assetDirectoryTypeId:any;

  assetDataList:any;

  assetCategoryName:any;

  isDataLoaded = false;

  isError = false;
  alertMessage = '';


  unitFieldType = 'unitno';
  unitOrder = false;
  externalListData:any;
  externalData = '';
  isExternalDataLoaded = false;
  externalDeleteId = '';

  errorMessage = '';


  userTable: FormGroup;
  control: FormArray;
  mode: boolean;
  touchedRows: any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private apartmentService: ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private fb: FormBuilder

  ) {
    this.userTable = this.fb.group({
      tableRows: this.fb.array([])
    });
    this.route.params.subscribe(params => {
      if(this.router.url.indexOf('maintain/asset') !== -1){
        this.getAssetDirectoryItems();
      }
    });

  }


  getAssetDirectoryItems(){
    this.isDataLoaded = false;

    // tslint:disable-next-line:no-string-literal
    this.assetDirectoryTypeId = this.route.params['value'].id;

    const params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      directoryTypeId: this.assetDirectoryTypeId
    }

    this.apartmentService.getAllDirectoryRecordsByDirectoryTypeId(params).subscribe((res:any) => {

      this.assetDataList = res;




      // if(res == undefined || res.length == 0){
      //   this.assetCategoryName = ""
      // }
      // else {
      //   this.assetCategoryName = res[0].categoryName
      const control =  this.userTable.get('tableRows') as FormArray;
      control.clear();

      // tslint:disable-next-line:no-shadowed-variable
      this.assetDataList.forEach((element, index) => {
        const formcontrol =  this.userTable.get('tableRows') as FormArray;
        formcontrol.push(this.fb.group({
          directoryRecordId: [element.directoryRecordId],
          serviceType: [element.serviceName, Validators.required],
          email: [element.email, [Validators.email, Validators.required]],
          contactperson: [element.contactPersonName, Validators.required],
          phone: [element.phone, [Validators.required, Validators.maxLength(10)]],
          isEditable: [false]
        }));
      });
      this.addRow();

      // const formcontrol =  this.userTable.get('tableRows') as FormArray;
      //   _.each(this.assetDataList, function(item, i, formcontrol: FormArray, fb: FormBuilder) {
      //     debugger;


      //   })

      // }



      this.isDataLoaded = true;

    },
    error => {
      this.isDataLoaded = true;
      this.isError = true;
      this.alertMessage = 'Some error occured';
    });
  }

  isNoItemsAvailable(){
    return this.assetDataList.length === 0 ? false : false;
  }

  ngOnInit(): void {
    this.isExternalDataLoaded = true;

    this.touchedRows = [];
    this.userTable = this.fb.group({
      tableRows: this.fb.array([])
    });
  }

  ngAfterViewInit() {
    this.control = this.userTable.get('tableRows') as FormArray;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType === type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  initiateForm(): FormGroup {
    return this.fb.group({
      directoryRecordId: [''],
      serviceType: ['', Validators.required],
      email: ['', [Validators.email, Validators.required]],
      contactperson: ['', Validators.required],
      phone: ['', [Validators.required, Validators.maxLength(10)]],
      isEditable: [true]
    });
  }


  addRow() {
    const control =  this.userTable.get('tableRows') as FormArray;
    control.push(this.initiateForm());
  }

  deleteRow(index: number, group: FormGroup) {
    const control =  this.userTable.get('tableRows') as FormArray;
    if(control.length > 1) {
    control.removeAt(index);

    const params = {
      // tslint:disable-next-line:radix
      DirectoryRecordId: parseInt(group.get('directoryRecordId').value),
      deleteBy: 1
    }

    this.apartmentService.deleteDirectoryRecord(params).subscribe((res:any) => {

      if(res.message) {
        this.sharedService.setAlertMessage(' deleted successfully');
      }
      else {
      }

    });
  } else {
    this.sharedService.setAlertMessage('record not delete');
  }
  }

  editRow(group: FormGroup) {
    group.get('isEditable').setValue(true);
  }

  doneRow(group: FormGroup) {
    group.get('isEditable').setValue(false);
    const recordID = group.get('directoryRecordId').value;

    const params = {
      directoryRecord:{
      // tslint:disable-next-line:radix
      directoryRecordId: recordID != "" ? parseInt(recordID) : 0,
      // tslint:disable-next-line:no-string-literal
      directoryTypeId: this.route.params['value'].id,
      // tslint:disable-next-line:radix
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      serviceName: group.get('serviceType').value,
      phone: group.get('phone').value,
      contactPersonName: group.get('contactperson').value,
      email: group.get('email').value,
      workingHours: '3',
      address: '',
      comment1: '',
      comment2: '',
      isActive: true,
      insertedBy: 1,
      insertedOn: new Date().toISOString(),
      updatedBy: 1,
      updatedOn: new Date().toISOString()
    }
    }


    if(recordID !== '') {

      this.apartmentService.updateDirectoryRecords(params).subscribe((res:any) => {

        if(res.message) {
          this.sharedService.setAlertMessage(' updated successfully');
        }
        else {
        }

      });
    } else {
      this.apartmentService.addDirectoryRecords(params).subscribe((res:any) => {

        if(res.message) {
          this.sharedService.setAlertMessage(' added successfully');
        }
        else {
        }

      });
    }
  }

  saveUserDetails() {
    console.log(this.userTable.value);
  }

  get getFormControls() {
    const control = this.userTable.get('tableRows') as FormArray;
    return control;
  }

  submitForm() {
    const control = this.userTable.get('tableRows') as FormArray;
    this.touchedRows = control.controls.filter(row => row.touched).map(row => row.value);
    console.log(this.touchedRows);
  }

}
