import { Component, OnInit, AfterViewInit, HostListener  } from '@angular/core';

import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { ApartmentService } from 'src/app/api/controllers/Apartment';

@Component({
  selector: 'app-maintain-directory',
  templateUrl: './maintain-directory.component.html',
  styleUrls: ['./maintain-directory.component.scss']
})
export class MaintainDirectoryComponent implements OnInit, AfterViewInit {

  selectedTab:any;
  
  itemPositions:any = [];
  currentPosition:any = 0;

  totalTabItems:any;
  
  tab:any;
  tabItems:any;

  assetCategoriesData:any;

  isDataLoaded:boolean = false;
  
  constructor(
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private apartmentService: ApartmentService
  ) { }


  showTab(type){
  	this.selectedTab = type;
  }

  isActive(type) {
    return this.selectedTab == type ? true : false;
  }

  ngOnInit() {
    this.selectedTab = 'external';
  }


  moveTabRight(){
  	if (this.currentPosition + 1 < this.itemPositions.length) {
      document.getElementById("directory-tabs").style.left = "-" + this.itemPositions[++this.currentPosition] + "px";
  	}
    
  }

  moveTabLeft(){
    if (this.currentPosition - 1 >= 0) {
      document.getElementById("directory-tabs").style.left = "-" + this.itemPositions[--this.currentPosition] + "px";
    }
  }

  widthOfList() {

    this.itemPositions = [];
  	var marginWidth = 60;

  	var wrapperWidth = document.querySelector('#directory-wrapper').clientWidth - marginWidth;

  	var itemsWidth = 0;
  	this.itemPositions.push(0);

  	_.each(this.tabItems, (item)=> {
  		var itemWidth = item.offsetWidth;
  	    itemsWidth += itemWidth;
  	    if (itemsWidth > wrapperWidth) {
  	      this.itemPositions.push( itemsWidth - wrapperWidth ); 
  	    }
  	});

  	return itemsWidth;

  }


  isMobileView() {
    return window.innerWidth <= 576 ? true : false;
  }

  @HostListener('window:resize', ['$event'])
  onWindowResize(event) {
    this.widthOfList();
    if(this.isMobileView()){
      document.getElementById("directory-tabs").style.left = 0 + 'px';
      this.currentPosition = 0;
    }
  }

  ngAfterViewInit() {

    //get all asset categories

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.apartmentService.getAllDirectoryTypeByApartmentId(params).subscribe((res:any) => {

      this.isDataLoaded = true;

      this.assetCategoriesData = res.filter(item => {
        return item.isActive;
      });
      
      setTimeout(() => {

        this.tab = document.getElementById('directory-tabs');
        this.tabItems = this.tab.getElementsByTagName("li");
        this.totalTabItems = this.tabItems.length;

        this.widthOfList();

      }, 1000);


    });

    
    // this.lookupService.getLookupValueByLookupTypeId(19).subscribe((res:any) => {

    //   this.isDataLoaded = true;

    //   this.assetCategoriesData = res.filter(item => {
    //     return item.isActive;
    //   });
      
    //   setTimeout(() => {

    //     this.tab = document.getElementById('directory-tabs');
    //     this.tabItems = this.tab.getElementsByTagName("li");
    //     this.totalTabItems = this.tabItems.length;

    //     this.widthOfList();

    //   }, 1000);


    // });

  



  }

}
