import { Component, OnInit } from '@angular/core';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-directory-setup',
  templateUrl: './directory-setup.component.html',
  styleUrls: ['./directory-setup.component.scss']
})
export class DirectorySetupComponent implements OnInit {

  directoryType = '';
  directoryTypeData: any;
  isDirectoryTypeLoaded = false;
  errorMessage = '';
  isError = false;

  directory: any;
  allDirectory: any;

  wpData = '';

  unitFieldType = 'directoryTypeId';
  unitOrder = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any = 0;
  totalItems:any = 0;
  itemLimit:any = 20;
  totalFilterItems:any;


  directoryTypeId=0;

  constructor(
    private apartmentService: ApartmentService,
    private sharedService: SharedService,
    private cookieService: CookieService
  ) { }

  ngOnInit(): void {
    this.directoryTypeId=0;
    this.isDirectoryTypeLoaded = true;
    this.directory = {
      directoryType : '',
      directoryDescription: ''
    };

    this.getDirectoryType();
  }

  submitDirectoryTypeForm(form) {
    const params = {
      directoryType: {
        directoryTypeId: this.directoryTypeId === 0 ? 0 : this.directoryTypeId,
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        directoryTypeName: this.directory.directoryType,
        description: this.directory.directoryDescription,
        isActive: true,
        insertedBy: parseInt(this.cookieService.get('userId')),
        insertedOn: new Date().toISOString(),
        updatedBy: parseInt(this.cookieService.get('userId')),
        updatedOn: new Date().toISOString(),
        directoryRecords: []
      }
    }

if( this.directoryTypeId === 0){
    this.apartmentService.addDirectoryType(params).subscribe((res:any) => {

      if(res.message) {
        this.sharedService.setAlertMessage(' added successfully');
        this.directoryTypeId=0;
        this.directory = {
          directoryType : '',
          directoryDescription: ''
        };
        this.getDirectoryType();
      }
      else {
      }

    });
  }
 else {
  this.apartmentService.updateDirectoryType(params).subscribe((res:any) => {

    if(res.message) {
      this.sharedService.setAlertMessage(' updated successfully');
      this.directoryTypeId=0;
      this.directory = {
        directoryType : '',
        directoryDescription: ''
      };
      this.getDirectoryType();
    }
    else {
    }

  });

}
}

  // Get Dkirectory Type
  getDirectoryType(){
    const params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.apartmentService.getAllDirectoryTypeByApartmentId(params).subscribe((res:any) => {
      this.allDirectory = res;
      this.isDirectoryTypeLoaded = true;
      this.totalItems = this.allDirectory.length;
      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }
    })
  }


  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType === type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }


   // Get Directory Type By DirectoryTypeId
   getDirectoryTypeByDirectoryTypeId(typeId){
    const params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      directoryTypeId: parseInt(typeId)
    }

    this.directoryTypeId=typeId;

    this.apartmentService.getDirectoryTypeByDirectoryTypeId(params).subscribe((res:any) => {
      const resDirectory = res;

      this.directory = {
        directoryType : resDirectory[0].directoryTypeName,
        directoryDescription: resDirectory[0].description
      };
    });
  }

   // delete Directory Type By DirectoryTypeId
   deleteByDirectoryTypeId(typeId){
    const params = {
      DirectoryTypeId: parseInt(typeId),
      deleteBy: parseInt(this.cookieService.get('userId'))
    }

    this.directoryTypeId=typeId;

    this.apartmentService.deleteDirectoryType(params).subscribe((res:any) => {
      if(res.message) {
        this.sharedService.setAlertMessage(' deleted successfully');
        this.getDirectoryType();
      }
    },
    (err:any)=>{
      this.sharedService.setAlertMessage(err);
    });
  }

}
